<template>
  <div class="card">
    <transition name="cardAnim">
      <div class="card-more" v-if="item.openCardState">
        <transition name="imageOpenAnim">
          <div class="fixedImage" v-if="this.openImage" @click="imageClose ()">
            <picture>
              <source v-bind:srcset=item.srcsetFour>
              <img v-bind:src=item.srcFour alt="">
            </picture>
          </div>
        </transition>
        <div class="row">
          <div class="col">
            <div class="col-body">
              <div class="filter">
                <form>
                  <div class="image-preview" @mouseenter="hoverImage ('First')" @click="hoverImage ('First')" v-bind:class="{ active: this.image === 'First' }">
                    <picture>
                      <source v-bind:srcset=item.srcsetFirst>
                      <img v-bind:src=item.srcFirst alt="">
                    </picture>
                  </div>
                  <div class="image-preview" @mouseenter="hoverImage ('Secondly')" @click="hoverImage ('Secondly')" v-bind:class="{ active: this.image === 'Secondly' }">
                    <picture>
                      <source v-bind:srcset=item.srcsetSecondly>
                      <img v-bind:src=item.srcSecondly alt="">
                    </picture>
                  </div>
                  <div class="image-preview" @mouseenter="hoverImage ('Third')" @click="hoverImage ('Third')" v-bind:class="{ active: this.image === 'Third' }">
                    <picture>
                      <source v-bind:srcset=item.srcsetThird>
                      <img v-bind:src=item.srcThird alt="">
                    </picture>
                  </div>
                </form>
              </div>
              <div class="image-view">
                <transition name="imageAnim">
                  <div class="image" v-if="this.image === 'First'">
                    <picture>
                      <source v-bind:srcset=item.srcsetFirst>
                      <img v-bind:src=item.srcFirst alt="">
                    </picture>
                  </div>
                </transition>
                <transition name="imageAnim">
                  <div class="image" v-if="this.image === 'Secondly'">
                    <picture>
                      <source v-bind:srcset=item.srcsetSecondly>
                      <img v-bind:src=item.srcSecondly alt="">
                    </picture>
                  </div>
                </transition>
                <transition name="imageAnim">
                  <div class="image"  v-if="this.image === 'Third'">
                    <picture>
                      <source v-bind:srcset=item.srcsetThird>
                      <img v-bind:src=item.srcThird alt="">
                    </picture>
                  </div>
                </transition>
                <div class="links">
                  <button @click="imageOpen()">
                    <svg width="20" height="14" viewBox="0 0 20 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                      <ellipse cx="10" cy="7" rx="9" ry="6" stroke="#171716"/>
                      <circle cx="10" cy="7" r="3.5" stroke="#171716"/>
                    </svg>
                    View in a room
                  </button>
                  <button>
                    <svg width="16" height="20" viewBox="0 0 16 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                     <path fill-rule="evenodd" clip-rule="evenodd" d="M7.48694 2.97042L4.8354 5.6041L3.95451 4.71723L7.55405 1.14195L7.97992 0.718946L8.41977 1.12738L12.0439 4.49262L11.1933 5.40862L8.73694 3.1277V13.4759H7.48694V2.97042ZM0.664185 8.65136H1.28918H4.60959V9.90136H1.91418V17.7942H14.1378V9.90136H11.6096V8.65136H14.7628H15.3878V9.27636V18.4192V19.0442H14.7628H1.28918H0.664185V18.4192V9.27636V8.65136Z" fill="#161412"/>
                    </svg>
                    Share
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div class="col">
            <div class="col-body">
              <div class="col-body__top">
                <div class="back">
                  <button type="button" @click="cardStateFalse ()">
                    <svg width="18" height="18" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg">
                      <path fill-rule="evenodd" clip-rule="evenodd" d="M8.58484 16.8927L9.11338 17.4248L10.1776 16.3677L9.64908 15.8356L3.365 9.50889H16.312H17.062V8.00889H16.312H3.29483L9.22313 1.62457L9.73347 1.07497L8.63428 0.0542946L8.12394 0.60389L1.01955 8.25478L0.529419 8.7826L1.03702 9.29365L8.58484 16.8927Z" fill="#161412"/>
                    </svg>
                    back
                  </button>
                </div>
                <h5>{{ item.name }}</h5>
                <span class="artist">{{ item.artistName }}</span>
                <span class="shortDescription">{{ item.shortDescription }}</span>
                <span class="description">{{ item.description }}</span>
              </div>
              <div class="col-body__bottom">
                <span class="price">
                  $ {{ item.price }}
                </span>
                <a class="location" href="">
                  <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <ellipse cx="12" cy="12.3719" rx="6" ry="8.37187" stroke="#65635F"/>
                    <circle cx="12" cy="9.5" r="1.5" stroke="#65635F"/>
                  </svg>
                  {{ item.location }}
                </a>
                <span class="delivery">
                  <svg width="17" height="16" viewBox="0 0 17 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <rect x="1.93333" y="4.73333" width="13.0667" height="10.2667" stroke="#65635F"/>
                    <path d="M6.5 7.53334L10.5 7.5" stroke="#65635F" stroke-linecap="square"/>
                    <rect x="1" y="1" width="14.9333" height="3.73333" stroke="#65635F"/>
                  </svg>
                  {{ item.delivery }}
                </span>
                <button class="button" @click="deleteBasketState ()" v-bind:class="{ active: item.basketState }" v-if="item.basketState">Remove from cart</button>
                <button class="button" @click="basketStateToggle ()" v-bind:class="{ active: item.basketState }" v-else>Add to cart</button>
                <small>Taxes and shipping fees will apply upon checkout</small>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <div class="card-body">
      <div class="main">
        <div class="icon">
          <svg width="20" height="14" viewBox="0 0 20 14" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path fill-rule="evenodd" clip-rule="evenodd" d="M3.91339 3.17338C2.3741 4.19958 1.5 5.56234 1.5 7C1.5 8.43766 2.3741 9.80042 3.91339 10.8266C5.44918 11.8505 7.59966 12.5 10 12.5C12.4003 12.5 14.5508 11.8505 16.0866 10.8266C17.6259 9.80042 18.5 8.43766 18.5 7C18.5 5.56234 17.6259 4.19958 16.0866 3.17338C14.5508 2.14952 12.4003 1.5 10 1.5C7.59966 1.5 5.44918 2.14952 3.91339 3.17338ZM3.35869 2.34133C5.08026 1.19362 7.42977 0.5 10 0.5C12.5702 0.5 14.9197 1.19362 16.6413 2.34133C18.3594 3.48672 19.5 5.12395 19.5 7C19.5 8.87605 18.3594 10.5133 16.6413 11.6587C14.9197 12.8064 12.5702 13.5 10 13.5C7.42977 13.5 5.08026 12.8064 3.35869 11.6587C1.64062 10.5133 0.5 8.87605 0.5 7C0.5 5.12395 1.64062 3.48672 3.35869 2.34133Z" fill="white"/>
            <path fill-rule="evenodd" clip-rule="evenodd" d="M10 4C8.34315 4 7 5.34315 7 7C7 8.65685 8.34315 10 10 10C11.6569 10 13 8.65685 13 7C13 5.34315 11.6569 4 10 4ZM6 7C6 4.79086 7.79086 3 10 3C12.2091 3 14 4.79086 14 7C14 9.20914 12.2091 11 10 11C7.79086 11 6 9.20914 6 7Z" fill="white"/>
          </svg>
        </div>
        <div class="img" @click="cardStateTrue ()">
          <picture>
            <source v-bind:srcset=item.srcsetFirst>
            <img v-bind:src=item.srcFirst alt="">
          </picture>
        </div>
        <h6>{{ item.name }}</h6>
        <div class="artist">{{ item.artistName }}
          <span class="shortDescription">{{ item.shortDescription }}</span>
          <span class="description">{{ item.shortDescription }}</span>
        </div>
      </div>
      <div class="info">
        <span class="price">$ {{ item.price }}</span>
        <div class="buttons">
          <button class="button button-grey">
            <svg width="16" height="20" viewBox="0 0 16 20" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path fill-rule="evenodd" clip-rule="evenodd" d="M7.48694 2.97042L4.8354 5.6041L3.95451 4.71723L7.55405 1.14195L7.97992 0.718946L8.41977 1.12738L12.0439 4.49262L11.1933 5.40862L8.73694 3.1277V13.4759H7.48694V2.97042ZM0.664185 8.65136H1.28918H4.60959V9.90136H1.91418V17.7942H14.1378V9.90136H11.6096V8.65136H14.7628H15.3878V9.27636V18.4192V19.0442H14.7628H1.28918H0.664185V18.4192V9.27636V8.65136Z" fill="#161412"/>
            </svg>
          </button>
          <button class="button" @click="deleteBasketState ()" v-bind:class="{ active: item.basketState }" v-if="item.basketState">
            <svg width="21" height="18" viewBox="0 0 21 18" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path fill-rule="evenodd" clip-rule="evenodd" d="M2.71053 0.375H0V1.625H2.23715L5.04951 11.6176L5.17776 12.0732H5.65114H17.0146H17.477L17.6122 11.631L20.1043 3.48205L20.3513 2.67427H19.5066H5.65114V3.92427H18.6619L16.5521 10.8232H6.12452L3.31216 0.830675L3.18391 0.375H2.71053ZM7.42857 14.1964C6.82697 14.1964 6.33928 14.6841 6.33928 15.2857C6.33928 15.8873 6.82697 16.375 7.42857 16.375C8.03016 16.375 8.51785 15.8873 8.51785 15.2857C8.51785 14.6841 8.03016 14.1964 7.42857 14.1964ZM5.08928 15.2857C5.08928 13.9938 6.13661 12.9464 7.42857 12.9464C8.72052 12.9464 9.76785 13.9938 9.76785 15.2857C9.76785 16.5777 8.72052 17.625 7.42857 17.625C6.13661 17.625 5.08928 16.5777 5.08928 15.2857ZM15.4286 14.1964C14.827 14.1964 14.3393 14.6841 14.3393 15.2857C14.3393 15.8873 14.827 16.375 15.4286 16.375C16.0302 16.375 16.5179 15.8873 16.5179 15.2857C16.5179 14.6841 16.0302 14.1964 15.4286 14.1964ZM13.0893 15.2857C13.0893 13.9938 14.1366 12.9464 15.4286 12.9464C16.7205 12.9464 17.7679 13.9938 17.7679 15.2857C17.7679 16.5777 16.7205 17.625 15.4286 17.625C14.1366 17.625 13.0893 16.5777 13.0893 15.2857Z" fill="#161412"/>
            </svg>
          </button>
          <button class="button" @click="basketStateToggle ()" v-bind:class="{ active: item.basketState }" v-else>
            <svg width="21" height="18" viewBox="0 0 21 18" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path fill-rule="evenodd" clip-rule="evenodd" d="M2.71053 0.375H0V1.625H2.23715L5.04951 11.6176L5.17776 12.0732H5.65114H17.0146H17.477L17.6122 11.631L20.1043 3.48205L20.3513 2.67427H19.5066H5.65114V3.92427H18.6619L16.5521 10.8232H6.12452L3.31216 0.830675L3.18391 0.375H2.71053ZM7.42857 14.1964C6.82697 14.1964 6.33928 14.6841 6.33928 15.2857C6.33928 15.8873 6.82697 16.375 7.42857 16.375C8.03016 16.375 8.51785 15.8873 8.51785 15.2857C8.51785 14.6841 8.03016 14.1964 7.42857 14.1964ZM5.08928 15.2857C5.08928 13.9938 6.13661 12.9464 7.42857 12.9464C8.72052 12.9464 9.76785 13.9938 9.76785 15.2857C9.76785 16.5777 8.72052 17.625 7.42857 17.625C6.13661 17.625 5.08928 16.5777 5.08928 15.2857ZM15.4286 14.1964C14.827 14.1964 14.3393 14.6841 14.3393 15.2857C14.3393 15.8873 14.827 16.375 15.4286 16.375C16.0302 16.375 16.5179 15.8873 16.5179 15.2857C16.5179 14.6841 16.0302 14.1964 15.4286 14.1964ZM13.0893 15.2857C13.0893 13.9938 14.1366 12.9464 15.4286 12.9464C16.7205 12.9464 17.7679 13.9938 17.7679 15.2857C17.7679 16.5777 16.7205 17.625 15.4286 17.625C14.1366 17.625 13.0893 16.5777 13.0893 15.2857Z" fill="#161412"/>
            </svg>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
export default {
  name: "List",
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      image: 'First'
    }
  },
  methods: {
    cardStateTrue () {
      this.item.openCardState = true
      if (this.item.openCardState === true) {
        document.body.style.overflow = 'hidden';
      } else {
        document.body.style.overflow = 'auto';
      }
    },
    cardStateFalse () {
      this.item.openCardState = false
      if (this.item.openCardState === true) {
        document.body.style.overflow = 'hidden';
      } else {
        document.body.style.overflow = 'auto';
      }
    },
    ...mapActions([
      'cartTargUpd',
      'imageUpdOpen',
      'imageUpdClose'
    ]),
    cartNull () {
      this.cartTargUpd()
    },
    basketStateToggle () {
      this.item.basketState = true
      this.$emit('addToCart', this.item)
      this.cartNull ()
    },
    deleteBasketState () {
      this.item.basketState = false
      this.$emit('deleteBasketState')
      this.cartNull ()
    },
    hoverImage (item) {
      this.image = item
    },
    imageOpen () {
      this.imageUpdOpen()
    },
    imageClose () {
      this.imageUpdClose()
    }
  },
  computed: {
    ...mapGetters([
        'openImage'
    ])
  }
}
</script>

<style scoped lang="scss">
// Const
$font-1: 'Karla', Arial, Helvetica, sans-serif;
$font-2: 'Shippori Mincho', 'Times New Roman', Times, serif;

$c-black: #161412;
$c-white: #fff;
$c-grey-1: #E7E7E1;
$c-grey-2: #F4F4F2;
$c-grey-3: #65635F;

.card {
  width: 230px;
  height: auto;
  position: relative;
  margin: 0 10px;
  padding: 40px;
  background: #FFFFFF;
  transition: .2s linear all;
  img {
    width: 230px;
    height: 100%;
    object-fit: contain;
    object-position: center;
  }
  &:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.08);
    .card-body {
      .shortDescription {
        opacity: 0;
      }
      .description {
        opacity: 1;
      }
      .info {
        opacity: 1;
      }
      .img:after {
        opacity: 1;
      }
      .icon {
        opacity: 1;
      }
    }
  }
  &-body {
    position: relative;
    z-index: 2;
    .img {
      position: relative;
      overflow: hidden;
      cursor: pointer;
      &:after {
        position: absolute;
        content: '';
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: rgba(0,0,0,.2);
        opacity: 0;
        transition: .2s linear all;
        z-index: 1;
      }
    }
    .icon {
      position: absolute;
      z-index: 1;
      top: 30%;
      left: 50%;
      transform: translate(-50%, -50%);
      opacity: 0;
      transition: .2s linear all;
      svg {
        width: 50px;
        height: 50px;
      }
    }
    h6 {
      margin-bottom: 6px;
      margin-top: 16px;
    }
    .artist {
      font-family: $font-2;
      font-size: 1.6rem;
      line-height: 145%;
      font-weight: 700;
      color: $c-grey-3;
      display: block;
      position: relative;
      margin-bottom: 65px;
    }
    .shortDescription {
      font-family:$font-2;
      font-size: 1.6rem;
      line-height: 145%;
      font-weight: 300;
      color: $c-grey-3;
      font-style: italic;
      transition: .2s linear all;
      opacity: 1;

      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      width: 230px;

      position: absolute;
      top: 22px;
      left: 0;
    }
    .description {
      font-family:$font-2;
      font-size: 1.6rem;
      line-height: 145%;
      font-weight: 300;
      color: $c-grey-3;
      font-style: italic;

      position: absolute;
      top: 22px;
      left: 0;

      transition: .2s linear all;
      opacity: 0;

      display: inline-block;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      display: -webkit-box;
      white-space: normal;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 230px;
    }
    .info {
      display: flex;
      justify-content: space-between;
      align-items: center;
      opacity: 0;
      transition: .2s linear all;
      overflow: hidden;
    }
    .price {
      font-style: normal;
      font-weight: bold;
      font-size: 1.6rem;
      line-height: 145%;
      color: $c-black;
    }
    .buttons {
      display: flex;
    }
    .button {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0;
      width: 32px;
      height: 32px;
      transition: .2s linear all;
      margin-right: 8px;
      &:last-child {
        margin-right: 0;
      }
      path {
        transition: .2s linear all;
        fill: $c-white;
      }
      &:hover {
        background-color: $c-white;
        path {
          fill: $c-black;
        }
      }
      &.active {
        background-color: #fd2d2d;
        border-color: #fd2d2d;
        path {
          fill: $c-white;
        }
      }
    }
    .button-grey {
      background-color: $c-grey-1;
      border-color: $c-grey-1;
      path {
        fill: $c-black;
      }
      &:hover {
        background-color: $c-white;
        border-color: $c-black;
        path {
          fill: $c-black;
        }
      }
    }
  }
}
.card-more {
  position: fixed;
  width: 100vw;
  top: 0;
  left: 0;
  background-color: #fff;
  z-index: 4;
  height: 100vh;
  opacity: 1;
  transition: .2s linear all;
  box-sizing: border-box;
  margin-top: 83px;
  @media (max-width: 997.98px) {
    margin-top: 62px;
  }
  .row {
    display: flex;
    height: 100%;
    position: relative;
    @media (max-width: 997.98px) {
      flex-wrap: wrap;
      overflow: auto;
    }
    .col {
      flex: 0 0 50%;
      max-width: 50%;
      height: 100%;
      @media (max-width: 997.98px) {
        flex: 0 0 100%;
        max-width: 100%;
        min-height: 90vh;
        height: auto;
      }
      &:first-child {
        background-color: $c-grey-2;
        display: flex;
        justify-content: center;
        align-items: center;
        .col-body {
          flex-direction: row;
          @media (max-width: 575.98px) {
            flex-direction: column;
            justify-content: center !important;
            align-items: center;
            .filter {
              form {
                display: flex;
                margin-bottom: 20px;
                .image-preview {
                  margin-top: 0;
                }
              }
            }
            .image-view {
              margin: 0;
            }
          }
        }
      }
      &-body {
        padding: 100px;
        display: flex;
        flex-direction: column;
        height: 100%;
        max-height: 100%;
        box-sizing: border-box;
        justify-content: space-between;
        @media (max-width: 575.98px) {
          padding: 20px;
        }
        .filter {
          display: flex;
          align-items: center;
        }
        .image-view {
          display: flex;
          flex-direction: column;
          justify-content: center;
        }
        &__top {
          position: relative;
          h5 {
            margin-bottom: 10px;
          }
          .artist {
            font-family: $font-2;
            font-size: 2.2rem;
            line-height: 145%;
            color: $c-black;
            display: block;
            margin-bottom: 30px;
          }
          .shortDescription {
            font-family: $font-2;
            font-size: 1.6rem;
            line-height: 145%;
            color: $c-grey-3;
            display: block;
            margin-bottom: 30px;
            max-width: 390px;
          }
          .description {
            font-family: $font-2;
            font-size: 1.6rem;
            line-height: 145%;
            color: $c-grey-3;
            display: block;
            max-width: 390px;
            margin-bottom: 30px;
          }
          &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 1px;
            background-color: $c-grey-3;
          }
        }
        &__bottom {
          .price {
            font-style: normal;
            font-weight: bold;
            font-size: 2.4rem;
            line-height: 145%;
            color: $c-black;
            display: block;
          }
          .location, .delivery {
            font-size: 1.4rem;
            line-height: 145%;
            color: $c-grey-3;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            svg {
              width: 16px;
              height: 16px;
              object-fit: contain;
              object-position: center;
              margin-right: 10px;
            }
          }
          .location {
            margin-bottom: 10px;
            margin-top: 10px;
          }
          .delivery {
            margin-bottom: 20px;
          }
          .button {
            display: flex;
            width: 100%;
            justify-content: center;
            align-items: center;
            margin-bottom: 5px;
            &.active {
              background-color: #fd2d2d;
              border-color: #fd2d2d;
              &:hover {
                color: $c-white;
              }
            }
          }
          small {
            font-size: 1.4rem;
            line-height: 145%;
            color: $c-grey-3;
          }
        }
      }
      .back {
        button {
          border: none;
          background-color: transparent;
          display: flex;
          justify-content: center;
          align-items: center;
          text-transform: uppercase;
          font-style: normal;
          font-weight: normal;
          font-size: 1.4rem;
