export default {
    state: {
        cart: [],
        cartOpen: false,
        cartTarget: false,
        sumVal: 0
    },
    mutations: {
        setCart: (state, product) => {
            state.cart.push(product);
            let price = 0;
            for (let i = 0; i < state.cart.length; i++) {
                price = price + state.cart[i].price
            }
            state.sumVal = price
        },
        removeFromCart: (state, index) => {
            for (let i = state.cart.length; i--; ) {
                if (state.cart[i].id === index) {
                    state.cart.splice(i, 1);
                }
            }
            let price = 0;
            for (let i = 0; i < state.cart.length; i++) {
                price = price + state.cart[i].price
            }
            state.sumVal = price
        },
        cartUpdate: (state) => {
            state.cartOpen = !state.cartOpen
        },
        cartTargToggle: (state) => {
            state.cartTarget = state.cart.length
        }
    },
    actions: {
        addToCart ({commit}, product) {
            commit('setCart', product)
        },
        deleteFromCart ({commit}, index) {
            commit('removeFromCart', index)
        },
        cartUpd ({commit}) {
            commit('cartUpdate')
        },
        cartTargUpd ({commit}) {
            commit('cartTargToggle')
        }
    },
    getters: {
        cartState (state) {
            return state.cart
        },
        cartToggle (state) {
            return state.cartOpen
        },
        cartTarg (state) {
            return state.cartTarget
        }
    }
}
