<template>
  <div>
    <div class="wrapper">
      <div class="container">
        <button @click="prevSlide()" class="revert">
          <svg width="38" height="18" viewBox="0 0 38 18" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path fill-rule="evenodd" clip-rule="evenodd" d="M8.70862 1.38015L9.14693 0.934612L10.038 1.81124L9.5997 2.25678L3.2583 8.70278L36.5769 8.80108L37.3269 8.8033L37.3224 10.3033L36.5724 10.3011L3.20185 10.2026L9.23988 16.6428L9.66735 17.0988L8.75545 17.9537L8.32798 17.4978L1.18695 9.88109L0.7764 9.4432L1.19736 9.01531L8.70862 1.38015Z" fill="#161412"/>
          </svg>
        </button>
        <button @click="nextSlide()">
          <svg width="38" height="18" viewBox="0 0 38 18" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path fill-rule="evenodd" clip-rule="evenodd" d="M29.4822 16.8284L29.0465 17.2765L28.1502 16.4052L28.5859 15.9571L34.8889 9.47355L1.57037 9.57296L0.820373 9.5752L0.8159 8.0752L1.5659 8.07296L34.9365 7.9734L28.8603 1.56912L28.4302 1.11572L29.337 0.255372L29.7672 0.708777L36.9533 8.28297L37.3664 8.71842L36.948 9.1488L29.4822 16.8284Z" fill="#161412"/>
          </svg>
        </button>
      </div>
      <div class="v-carousel" :style="{ 'margin-left': '-' + (sliderSettings.slideWidth * sliderSettings.currentSlideIndex) + 'px' }">
        <CardItemGallery
            v-for="(item, index) in galleryList"
            v-bind:item = "item"
            :key="item.id"
            :item_data="item"
            @addToCart="basketStateToggle"
            @deleteBasketState = "deleteBasketState (index)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import CardItemGallery from "@/components/CardItemGallery";
import { mapGetters, mapActions } from 'vuex'

export default {
  name: "Slider",
  data () {
    return {
      sliderSettings: {
        currentSlideIndex: 0,
        sliderPreview: 5,
        slideWidth: 330
      }
    }
  },
  components: {
    CardItemGallery
  },
  computed: {
    ...mapGetters([
      'galleryList',
      'cartState'
    ])
  },
  methods: {
    prevSlide () {
      if (this.sliderSettings.currentSlideIndex > 0) {
        this.sliderSettings.currentSlideIndex--
      } else {

      }
    },
    nextSlide () {
      if (this.sliderSettings.currentSlideIndex < this.galleryList.length - 5) {
        this.sliderSettings.currentSlideIndex++
      } else {

      }
    },
    ...mapActions([
        'addToCart',
        'deleteFromCart',
    ]),
    basketStateToggle (data) {
      this.addToCart(data)
      this.$store.state.cart.cartOpen = true
    },
    deleteBasketState (index) {
      this.deleteFromCart(index)
      this.$store.state.cart.cartOpen = true
    }
  }
}
</script>

<style scoped lang="scss">
.wrapper {
  overflow: hidden;
}
.v-carousel {
  display: flex;
  transition: .2s linear all;
  align-items: center;
  padding: 100px 0;
  @media (max-width: 997.98px) {
    padding: 20px 0;
  }
}
button {
  border: none;
  background-color: transparent;
}
.container {
  display: flex;
  justify-content: space-between;
  margin-top: 100px;
  @media (max-width: 997.98px) {
    margin-top: 50px;
  }
}
</style>
