<template>
  <div class="cart">
    <div v-if="!cartTarg" class="text">
      <span class="null">Cart is empty</span>
    </div>
   <div class="card-list">
     <AppCartItem
         v-for="item in cartState"
         v-bind:item = "item"
     />
     <div v-if="cartTarg" class="sum">
       <span>Total</span>
       <span class="null">$ {{ $store.state.cart.sumVal }}</span>
     </div>
   </div>
  </div>
</template>

<script>
import AppCartItem from "@/components/AppCartItem";
import {mapActions, mapGetters} from 'vuex'
export default {
  name: "AppCart",
  components: {
    AppCartItem
  },
  computed: {
    ...mapGetters([
      'cartState',
      'cartTarg'
    ])
  }
}
</script>

<style scoped lang="scss">
.cart {
  z-index: -1;
  background-color: rgba(255,255,255,.8);
  backdrop-filter: blur(10px);
  padding: 10px 0;
  position: absolute;
  top: 100%;
  right: 0;
  width: 700px;
  opacity: 0;
  transition: .1s linear all;
  transform: translateX(30px);
  display: none;
  &.active {
    transform: translateX(0);
    opacity: 1;
    display: block;
  }
  @media (max-width: 997.98px) {
    padding: 5px 0;
    width: 500px;
  }
  @media (max-width: 575.98px ) {
    width: 100%;
    position: fixed;
    top: 56px;
  }
}
.null {
  padding: 10px 10px 0 10px;
  @media (max-width: 575.98px ) {
    padding: 10px 5px 0 5px;
  }
}
.text {
  animation-name: item;
  animation-duration: .1s;
  transition: .1s linear all;
}
.card-list {
  animation-name: item;
  animation-duration: .1s;
  transition: .1s linear all;
}
.sum {
  padding: 10px 10px 0 10px;
  display: flex;
  justify-content: space-between;
  position: relative;
  margin-top: 20px;
  @media (max-width: 575.98px ) {
    padding: 10px 5px 0 5px;
  }
  &:before {
    position: absolute;
    content: '';
    background-color: #ccc;
    width: 100%;
    margin: 0 10px;
    height: 1px;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    @media (max-width: 575.98px ) {
      margin: 0 5px;
    }
  }
  span {
    font-family: 'Shippori Mincho', 'Times New Roman', Times, serif;
    font-style: normal;
    font-weight: bold;
    font-size: 2.2rem;
    line-height: 145%;
    color: #161412;
  }
}
@keyframes item {
  from {
    opacity: 0;
    transform: translateY(-100%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
</style>
